:root{--PrimaryColor: hsl(199, 100%, 33%);--SecondaryColor: hsl(187, 85%, 43%);--gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));--whiteColor: hsl(0, 6%, 100%);--blackColor: ☐ hs1(201, 33%, 16%);--textColor: hsl(240, 4%, 36%);--whiteColorDeam: hsl(0, 0%, 93%);--greyText: rgb(199, 190, 190);--inputColor: rgb(239, 239, 239);--bodyColor:rgb(248, 240, 246);--cardBG:rgb(225, 225, 235)}.home{width:100%;position:relative;display:flex;align-items:center;justify-content:center;margin:auto}.home .overlay{position:absolute;height:100%;width:100%;background:#2f7b96;top:0;bottom:0;mix-blend-mode:hard-light}.home video{position:absolute;height:100%;width:100%;top:0;bottom:0;-o-object-fit:cover;object-fit:cover}.home .homeContent{width:100%;height:-moz-max-content;height:max-content;padding:8rem 1.5rem 3rem;display:flex;row-gap:3rem;flex-direction:column;align-items:stretch;justify-content:center;margin:auto;color:var(--whiteColorDeam);z-index:100}.home .homeContent .textDiv{padding:2rem 0}.home .homeContent .textDiv .smallText{font-size:13px;text-transform:uppercase}.home .homeContent .textDiv .homeTitle{position:relative;font-size:1.8rem}.home .homeContent .cardDiv{position:relative;padding:2rem 1rem;background:var(--whiteColor);border-radius:10px;gap:1rem}.home .homeContent .cardDiv label{display:block;color:var(--textColor);padding-bottom:10px;font-size:13px;font-weight:600}.home .homeContent .cardDiv .label_total{justify-content:space-between;align-items:center}.home .homeContent .cardDiv .label_total .total{color:var(--textColor);font-size:10px;padding-bottom:10px}.home .homeContent .cardDiv .input{height:40px;padding:0 1rem;background:var(--inputColor);border-radius:1rem}.home .homeContent .cardDiv .input input{background:none;border:none;outline:none;width:100%;font-size:13px;font-weight:500}.home .homeContent .cardDiv .input input::-moz-placeholder{font-size:13px;font-style:italic}.home .homeContent .cardDiv .input input::placeholder{font-size:13px;font-style:italic}.home .homeContent .cardDiv .icon{font-size:18px;color:var(--PrimaryColor) !important;margin-left:10px}.home .homeContent .cardDiv .input[type=range]{background:blue}.home .homeContent .searchOptions{position:absolute;background:var(--gradientColor);width:-moz-max-content;width:max-content;bottom:-0.7rem;-moz-column-gap:.8rem;column-gap:.8rem;padding:5px 1rem;border-radius:3rem;left:50%;transform:translate(-50%);justify-content:center;cursor:pointer}.home .homeContent .searchOptions:hover{background:var(--PrimaryColor)}.home .homeContent .searchOptions .icon{font-size:13px}.home .homeContent .homeFooterIcons{width:100%;justify-content:space-between}.home .homeContent .homeFooterIcons .icon{font-size:20px;margin:0 5px;color:var(--whiteColor);font-weight:600}.home .homeContent .homeFooterIcons .icon:hover{color:var(--PrimaryColor)}