:root{--PrimaryColor: hsl(199, 100%, 33%);--SecondaryColor: hsl(187, 85%, 43%);--gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));--whiteColor: hsl(0, 6%, 100%);--blackColor: ☐ hs1(201, 33%, 16%);--textColor: hsl(240, 4%, 36%);--whiteColorDeam: hsl(0, 0%, 93%);--greyText: rgb(199, 190, 190);--inputColor: rgb(239, 239, 239);--bodyColor:rgb(248, 240, 246);--cardBG:rgb(225, 225, 235);--cardHover: rgb(255, 255, 255)}.main{width:100%}.main .sectionTitle .title{position:relative;width:-moz-max-content;width:max-content;color:var(--textColor);margin:1rem 0;z-index:2}.main .sectionTitle .title::after{position:absolute;content:"";background:var(--SecondaryColor);height:5px;width:120px;right:0;bottom:5px;z-index:-1}.main .secContent{justify-content:center;gap:1.5rem}.main .secContent .singleDestination{height:100%;display:grid;padding:1rem;row-gap:10px;border-radius:10px;align-items:center;background:var(--cardBG);box-shadow:0 2px 4px rgba(140,140,140,.549);overflow:hidden}.main .secContent .singleDestination:hover{background:var(--cardHover);box-shadow:1 4px 4px rgba(85,85,114,.549);transition:.3s ease}.main .secContent .singleDestination .imageDiv{height:100%;width:100%;overflow:hidden}.main .secContent .singleDestination .imageDiv img{height:100%;width:100%;-o-object-fit:cover;object-fit:cover;transition:2s ease}.main .secContent .singleDestination .imageDiv:hover img{transform:scale(1, 1)}.main .secContent .singleDestination .cardInfo{padding:1rem}.main .secContent .singleDestination .cardInfo .desTitle{color:var(--blackColor);font-size:1.25rem}.main .secContent .singleDestination .cardInfo .continent{gap:.5rem}.main .secContent .singleDestination .cardInfo .continent .icon{color:var(--textColor);font-size:10px}.main .secContent .singleDestination .cardInfo .continent .name{color:var(--textColor);font-weight:400;font-size:14px}.main .secContent .singleDestination .cardInfo .fees{width:100%;justify-content:space-between;padding:1rem;margin:1rem 0;border-top:1.5px solid var(--greyText);border-bottom:1.5px solid var(--greyText)}.main .secContent .singleDestination .cardInfo .fees .grade{color:var(--textColor);max-width:100px;line-height:20px;display:flex;align-items:center;justify-content:center}.main .secContent .singleDestination .cardInfo .fees .grade small{text-align:center;background:var(--gradientColor);border-radius:1rem;color:var(--whiteColor);padding:1px 10px;font-size:10px}.main .secContent .singleDestination .cardInfo .fees .price{font-size:1.2rem;color:var(--blackColor)}.main .secContent .singleDestination .cardInfo .desc{font-size:13.5px;color:var(--textColor);font-weight:500}.main .secContent .singleDestination .cardInfo .btn{margin-top:1rem;color:var(--whiteColor);font-weight:600;justify-content:space-between;gap:1rem}.main .secContent .singleDestination .cardInfo .btn .icon{align-self:center}